import { useTranslation } from 'react-i18next';

// Bootstrap
import Container from 'react-bootstrap/Container';
import Col from 'react-bootstrap/Col';
import Row from 'react-bootstrap/Row';
import Button from 'react-bootstrap/Button';
import Image from 'react-bootstrap/Image';

// Components
import NavOne from '../NavOne';
import AppNavbar from '../Shared/AppNavbar/AppNavbar';
import BreadCrumbs from '../Shared/BreadCrumb/BreadCrumb';
import Icon from '../Shared/Icon/Icon';
import Rating from '../Shared/Rating/Rating';


// SCSS
import styles from './styles.module.scss';
import { MomentMonthFromTimeFormat } from '../../core/Mixins/MomentFormats';

interface IHeaderWithDescriptionProps {
  rate: number,
  btnText: string,
  onClick: () => void,
  hasVideo?: boolean,
  src: string,
  date: string
}

const HeaderWithDescription = ({
  rate,
  btnText,
  onClick,
  hasVideo = false,
  src,
  date
}: IHeaderWithDescriptionProps) => {
  const { t } = useTranslation();

  const breadCrumbs = ['الرئيسية', 'الندوات'];

  return (
    <div className='bg-blue-with-circle pb-0'>
      <NavOne />
      <AppNavbar />

      <Container fluid className="py-5 ">
        <Row>
          <Col xs={12} md={6}>
            <div className={`${styles.path} d-flex align-items-baseline justify-content-between flex-wrap-wrap`}>
              <BreadCrumbs breadCrumbs={breadCrumbs} />
              <div className="text-white">
                <Icon iconName='heart' hasStroke variant='primary' iconSize={32} className='bg-white py-1 px-2 rounded-pill me-2' />
                {t('HeaderWithDescription.favourite')}
              </div>
            </div>

            <div className='d-flex justify-content-between align-items-center mb-4'>
              <p className="my-3 text-white">
                <span className="mx-2">
                  <Icon iconName='calender-clock' variant='secondary' iconSize={28} />
                </span>
                {t("HeaderWithDescription.day")} {MomentMonthFromTimeFormat(date)}
              </p>
              <Rating readOnly defaultValue={rate} rateColor="white" />
            </div>

            <h3 className="mb-4 text-white">
              {t('HeaderWithDescription.description')}
            </h3>

            <Button variant='secondary' onClick={onClick} className="mt-3 text-white px-5 py-2"> {btnText} </Button>

          </Col>

          <Col xs={12} md={6} className="mx-auto">
            {
              hasVideo ? (
                <video width="100%" height={533} controls className='mt-4 mt-md-0'>
                  <source src={src} type="video/mp4" />
                </video>
              ) : (
                <Image src={src} width="100%" height={533} />
              )
            }
          </Col>
        </Row>
      </Container>

    </div>
  );
}

export default HeaderWithDescription;
