import { useTranslation } from 'react-i18next';

import Container from 'react-bootstrap/Container';
import ListGroup from 'react-bootstrap/ListGroup';
import Image from 'react-bootstrap/Image';

import './LecturersStyle.scss';

interface ILecturerProps {
  id: number | string,
  avatar: string,
  name: string,
  description: string
}

interface ILecturersProps {
  lecturersItems: ILecturerProps[]
}

const Lecturers = ({lecturersItems}: ILecturersProps) => {
  const { t } = useTranslation("courses");

  return (
    <Container fluid>
      <div className={`lecturers ${lecturersItems.length > 2 ? "lecturers--height" : ""}`}>
        <h5><mark className="bg-seconadry text-blue"> {t('presentationOfTheLecturers')} </mark></h5>

        <ListGroup className="mt-3" variant='flush'>
          {
            lecturersItems.map((lecturer) => (
              <ListGroup.Item className="d-flex mb-4 align-items-center border-0" key={lecturer.id}>
                <Image src={lecturer.avatar} width={52} height={52} className="rounded-pill" />
                <div className="ms-3">
                  <h6>{lecturer.name}</h6>
                  <small className="opacity-50">{lecturer.description}</small>
                </div>
              </ListGroup.Item>
            ))
          }
        </ListGroup>
      </div>
    </Container>
  )
};

export default Lecturers;