import { useTranslation } from 'react-i18next';
import { Link } from 'react-router-dom';

import Container from 'react-bootstrap/Container';
import Row from 'react-bootstrap/Row';
import Col from 'react-bootstrap/Col';
import ListGroup from 'react-bootstrap/ListGroup';
import Card from 'react-bootstrap/Card';
import Nav from 'react-bootstrap/Nav';

import Icon from '../Shared/Icon/Icon';

import styles from './styles.module.scss';


const About = () => {
  const { t } = useTranslation();

  const publishContentItems = [
    {
      id: 1,
      tag: "ندوات",
      des: "يوجد ندواتين كل اسبوع "
    },
    {
      id: 2,
      tag: "ندوات",
      des: "جميع النداوت مجانية من صفا"
    },
    {
      id: 3,
      tag: "ندوات",
      des: "خطوات و نصائح في مجال الاقتصاد"
    }
  ];

  const socialMedia = [
    {
      id: 1,
      name: "google",
      zoom: "sm"
    },
    {
      id: 2,
      name: "linkedin",
      zoom: "normal"
    }, {
      id: 3,
      name: "twitter",
      zoom: "normal"
    }, {
      id: 4,
      name: "facebook",
      zoom: "normal"
    }
  ];

  return (
    <div className={styles.about}>
      <Container fluid>
        <Row className="py-5">
          <Col xs={12} md={6}>
            <div className={styles.about__summary}>
              <Icon iconName="logoShape" variant='primary' iconSize={186} className="position-absolute" />
              <h2 className='text-blue'>{t('about.title')} </h2>
              <h6 className="my-3 text-secondary">{t('about.subTitle')} </h6>
              <p className="mb-4 text-blue fs-5">
                {t('about.description1')}
              </p>
              <p className='text-blue fs-5'>
                {t('about.description2')}
              </p>
            </div>
          </Col>
          <Col xs={12} md={6}>
            <Card className="rounded-3 bg-primary border-0  mt-3 mt-md-0 shadow-sm">
              <Card.Header className="bg-secondary rounded-3 h4 text-white d-flex justify-content-between align-items-center px-5 py-4">
                <span> {t("about.listTitle")} </span>
                <Nav as="ul">
                  {
                    socialMedia.map((item) => (
                      <Nav.Item as='li' key={item.id} className="ps-3">
                        <Link to={`/${item.name}`} >
                          <Icon iconName={item.name} iconSize={18} className={`zoom-${item.zoom}`} />
                        </Link>
                      </Nav.Item>
                    ))
                  }
                </Nav>
              </Card.Header>
              <Card.Body >
                <ListGroup variant='flush'>
                  {
                    publishContentItems.map((item) => (
                      <ListGroup.Item className="d-flex mb-4 align-items-center bg-transparent border-0" key={item.id}>
                        <Icon iconName="check" className="bg-orange rounded-pill p-3" iconSize={53} />
                        <div className="ms-3">
                          <p className="text-white opacity-40">{item.tag}</p>
                          <h4 className="text-white">{item.des}</h4>
                        </div>
                      </ListGroup.Item>
                    ))
                  }
                </ListGroup>
              </Card.Body>
            </Card>
          </Col>
        </Row>
      </Container>
    </div>
  );
};

export default About;
