import { Form, Button } from 'react-bootstrap';
import circlesStudent from '../../assets/images/circelStudent.png';
import student from '../../assets/images/student.png';
import question from '../../assets/images/question.png';
import styles from "./styles.module.scss";
import { useTranslation } from 'react-i18next';

export default function Contact() {

  const { t} = useTranslation();
  return (
    <div className={`${styles.contact} py-5`}>
      <div className={`${styles.student} w-50`}>
        <div className={styles.circels}>
          <img src={circlesStudent} alt="circles" className="w-100" />
        </div>
        <div className={styles.image}>
          <img src={student} alt="circlesStudent" className="w-100" />
        </div>
      </div>
      <div className={`${styles.question} p-5  w-50`}>
        <div className={`${styles.box} d-flex align-items-center`}>
          <div>
            <img src={question} alt="questionMark" />
          </div>
          <div className="info mx-3">
            <h5>  {t('contact.title')}</h5>
            <p> {t('contact.subTitle')} </p>
          </div>
        </div>
        <Form>
          <Form.Group className="my-4" controlId="exampleForm.ControlInput1">
            <Form.Label className="px-2"> {t('contact.label')}</Form.Label>
            <Form.Control
              className="mb-3"
              type="email"
              placeholder="احمد محمد"
            />
            <Form.Text className={` ${styles.formText} text-muted`} >
            {t('contact.error')}
            </Form.Text>
          </Form.Group>
          <Button variant="primary" type="submit">
            {t('contact.send')}
          </Button>
        </Form>
      </div>
    </div>
  );
}
