import { useTranslation } from "react-i18next";

import ListGroup from "react-bootstrap/ListGroup";
import Image from "react-bootstrap/Image";

import Icon from "../Shared/Icon/Icon";

import { MomentMonthFromTimeFormat } from "../../core/Mixins/MomentFormats";


interface ICommentProps {
  id: number | string,
  name: string,
  avatar: string,
  date: string,
  comment: string,
  rate: number,
}


const CommentsList = ({
  id,
  name,
  avatar,
  date,
  comment,
  rate,
}: ICommentProps) => {

  const { t } = useTranslation();

  return (
    <ListGroup.Item className="d-md-flex align-items-start px-0 pb-4 mb-4 text-center text-md-start">
      {
        avatar ? (
          <Image src={avatar} width={60} height={60} className="rounded-pill" />
        ) : (
          <div className="bg-primary opacity-20 rounded-circle d-inline-block" style={{width: "3.75rem", height: "3.75rem"}}>
            <Icon iconName="user" iconSize={60} />
          </div>
        )
      }
      <div className="ms-3">
        <div className="d-md-flex align-items-center justify-content-between">
          <div className="flex-grow-1">
            <p className="fs-7 text-blue"> {name} </p>

            <p className="fs-7 text-light-purple">{t('day')}  {MomentMonthFromTimeFormat(date)} </p>
          </div>
          <div>
            <Icon iconName="star" variant="warning" iconSize={15} className="align-baseline me-1" />
            {rate}
          </div>
        </div>
        <p className="fs-8 text-blue mt-3">{comment}</p>
      </div>
    </ListGroup.Item>
  )
}

export default CommentsList;