import { useEffect, useState } from "react";
import { useTranslation } from "react-i18next";
import { useParams } from "react-router-dom";
import About from "../../components/About";
import Comments from "../../components/Comments/Comments";
import Contact from "../../components/Contact";

import HeaderWithDescription from "../../components/HeaderWithDescription/HeaderWithDescription";
import Footer from "../../components/Layout/Footer/Footer";
import Lecturers from "../../components/Lecturers/Lecturers";

import WebinarsData from "../../core/Data/webinars";


const Webinar = () => {
  const { t } = useTranslation("webinars");

  const [webinar, setWebinar] = useState<any>({
    id: "",
    rate: 0,
    lecturers: [],
    date: "",
    comments: []
  });

  const { id } = useParams();

  useEffect(() => {
    try {
      const res = WebinarsData;
      const webinarObject = res.webinars.find((webinar) => webinar.id === id)
      setWebinar(webinarObject);

    } catch (e) {
      console.log(e)
    }
  }, [id]);

  const clickwatchHandled = () => {
    console.log("watch")
  }

  return (
    <>
      <HeaderWithDescription
        btnText={t('watch')}
        onClick={clickwatchHandled}
        rate={webinar.rate}
        hasVideo
        src="https://www.veed.io/view/e66d1c47-1388-441a-b727-6723d7d502c8"
        date={webinar.date}
      />
      <Lecturers lecturersItems={webinar.lecturers} />
      <About />
      <Comments comments={webinar.comments} />
      <Contact />
      <Footer />
    </>

  )
}

export default Webinar;